___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
}

body,
html {

  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;

  font-size: 12px;

  height: 100%;
  padding: 0;
  margin: 0;
}

a:link {
  text-decoration: none;
}

.content,
.content>div {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.content>.message {
  text-align: center;
  display: table;

  font-size: 16px;
  color: #111;
}

.content>.message .note {
  vertical-align: middle;
  text-align: center;
  display: table-cell;
}

.content .error .details {
  max-width: 500px;
  font-size: 12px;
  margin: 20px auto;
  text-align: left;
}

.content .error pre {
  border: solid 1px #CCC;
  background: #EEE;
  padding: 10px;
}

.content:not(.with-error) .error,
.content.with-error .intro,
.content.with-diagram .intro {
  display: none;
}


.content .canvas,
.content.with-error .canvas {
  visibility: hidden;
}

.content.with-diagram .canvas {
  visibility: visible;
}

.buttons {
  position: fixed;
  bottom: 20px;
  left: 20px;

  padding: 0;
  margin: 0;
  list-style: none;
}

.buttons li {
  display: inline-block;
  margin-right: 10px;
}

.buttons li .call_to_action {
  background: #DDD;
  border: solid 1px #666;
  display: inline-block;
  padding: 5px;
}

.buttons .call_to_action {
  opacity: 0.3;
}

.interactive_section {
  display: flex;
  justify-content: start;
}

.buttons .call_to_action.active {
  opacity: 1.0;
}

:root {
  --primary-color: royalblue;
}

.button {
  padding: 5px 16px;
  color: #000000d9;
  border: 1px solid #d9d9d9;
  background-color: transparent;
  border-radius: 2px;
  line-height: 1.4;
  box-shadow: 0 2px #00000004;
  cursor: pointer;
  transition: .3s;
  transform: scale(1);
}

.button:focus-visible {
  outline: 0;
}

.button::after {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  box-shadow: 0 0 0 6px var(--primary-color);
  opacity: 0;
  transition: .3s;
}

.button[type="primary"] {
  border-color: transparent;
  background-color: var(--primary-color);
  color: #fff;
  text-shadow: 0 -1px 0 rgb(0 0 0 / 12%);
  box-shadow: 0 2px #0000000b;
}

.button[type="dashed"] {
  border-style: dashed;
}

.button[type="text"] {
  border-color: transparent;
  box-shadow: none;
}

.button:not([type]):hover,
.button[type="dashed"]:hover,
.button[type="dashed"]:focus {
  color: var(--primary-color);
  border-color: currentColor;
}

.button[type="primary"]:hover {
  filter: brightness(1.1);
}

.button[type="text"]:hover,
.button[type="text"]:focus {
  background: rgba(0, 0, 0, .018);
}

.button.button:active {
  filter: brightness(.9);
}

.button.button:active:not([type="text"])::after {
  transition: 0s;
  box-shadow: none;
  opacity: 0.4;
}

.icon {
  width: 1em;
  margin-right: .5em;
  vertical-align: middle;
  fill: currentColor;
  transform: rotate(360deg);
  transition: .5s;
}

.button:active .icon {
  transform: rotate(0);
  transition: 0s;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,sBAAsB;AACxB;;AAEA;;;EAGE,2DAA2D;;EAE3D,eAAe;;EAEf,YAAY;EACZ,UAAU;EACV,SAAS;AACX;;AAEA;EACE,qBAAqB;AACvB;;AAEA;;EAEE,WAAW;EACX,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,cAAc;;EAEd,eAAe;EACf,WAAW;AACb;;AAEA;EACE,sBAAsB;EACtB,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;EACtB,gBAAgB;EAChB,aAAa;AACf;;AAEA;;;EAGE,aAAa;AACf;;;AAGA;;EAEE,kBAAkB;AACpB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,eAAe;EACf,YAAY;EACZ,UAAU;;EAEV,UAAU;EACV,SAAS;EACT,gBAAgB;AAClB;;AAEA;EACE,qBAAqB;EACrB,kBAAkB;AACpB;;AAEA;EACE,gBAAgB;EAChB,sBAAsB;EACtB,qBAAqB;EACrB,YAAY;AACd;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,0BAA0B;AAC5B;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,yBAAyB;EACzB,6BAA6B;EAC7B,kBAAkB;EAClB,gBAAgB;EAChB,2BAA2B;EAC3B,eAAe;EACf,eAAe;EACf,mBAAmB;AACrB;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,QAAQ;EACR,sBAAsB;EACtB,0CAA0C;EAC1C,UAAU;EACV,eAAe;AACjB;;AAEA;EACE,yBAAyB;EACzB,sCAAsC;EACtC,WAAW;EACX,sCAAsC;EACtC,2BAA2B;AAC7B;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,yBAAyB;EACzB,gBAAgB;AAClB;;AAEA;;;EAGE,2BAA2B;EAC3B,0BAA0B;AAC5B;;AAEA;EACE,uBAAuB;AACzB;;AAEA;;EAEE,+BAA+B;AACjC;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,UAAU;EACV,kBAAkB;EAClB,sBAAsB;EACtB,kBAAkB;EAClB,yBAAyB;EACzB,eAAe;AACjB;;AAEA;EACE,oBAAoB;EACpB,cAAc;AAChB","sourcesContent":["* {\n  box-sizing: border-box;\n}\n\nbody,\nhtml {\n\n  font-family: \"Helvetica Neue\", Helvetica, Arial, sans-serif;\n\n  font-size: 12px;\n\n  height: 100%;\n  padding: 0;\n  margin: 0;\n}\n\na:link {\n  text-decoration: none;\n}\n\n.content,\n.content>div {\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n}\n\n.content>.message {\n  text-align: center;\n  display: table;\n\n  font-size: 16px;\n  color: #111;\n}\n\n.content>.message .note {\n  vertical-align: middle;\n  text-align: center;\n  display: table-ce